$main-color:#1A512E;
$second-color:#970009;

.hidden{
  display: none !important;
}

body{
  background-color: #fff;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 25px;
  color: #000;
  position: relative;
  overflow-x: hidden;
  padding-bottom: 100px;
  background: url("../img/auto.svg") no-repeat bottom 0 right 0;
}

.hero{
  width: 100%;
  padding-top: 70px !important;
  position: relative;
  background: $main-color url("../img/circle-scatter-haikei.svg") no-repeat left 0 top 0;
  background-size: 100% 100%;
  margin-bottom: 0;
}


.hero_grid{
  margin-top: 120px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  position: relative;
  padding-bottom: 5px;
}

.hero_text{
  text-align: center;
  color: #fff;
  position: relative;
}

.hero_title{
  font-size: 90px;
  font-weight: 400;
}

.hero_about{
  font-size: 24px;
}

.hero_image{
  position: absolute;
  z-index: 2; 
  right: 0;
  min-height: 500px;
  width: 50%;
  margin-top: 200px;
  img{
    width: 100%;
    filter: drop-shadow(5px 5px 5px rgba(0,0,0,0.5));
  }
}

.btn-product{
  position: absolute;
  transform: translateY(100%) translateX(-50%);
  left: 50%;
  bottom: -100px;
  z-index: 2;
  font-size: 20px;
  padding: 10px 45px;
}


.hero_wave{
  position: absolute;
  bottom: -130px;
  z-index: 1;
  height: 130px;
  //svg{
  //  height: 100%;
  //}
}


.section{
  margin-top: 210px;
  position: relative;
  &.hero{
    margin-top: 0px;
  }
  &.gallery{
    .section_title{
      font-weight: lighter;
    }
    &::after{
      content: "";
      position: absolute;
      width: 400px;
      height: 300px;
      bottom: 0;
      left: -50px;
      z-index: 0;
      background: url("../img/truck.svg") bottom 0 left 0 no-repeat;
      background-size: 100% 100%;
      transform: scale(-1, 1) rotate(270deg);
    }
  }
}

.product_photos{
  display: flex;
  flex-wrap: wrap;
}

.product_photo_item{
  z-index: 2;
  display: block;
  position: relative;
  padding: 10px 60px;
  transition: all .3s ease-out;
  transform: translateZ(0);
  img{
    position: relative;
    z-index: 2;
    width: 100%;
    //transform: translateZ(0);
  }
  &:hover{
    scale: 1.1;
    filter: drop-shadow(10px 15px 3px rgba(0,0,0,0.5));
  }
}

@media(max-width: 991px){
  .hero_image{
    display: none;
  }


  .section::after{
    top:300px;
    transform: rotate(90deg) !important;
  }
}

@media(max-width: 577px){

  body{
    padding-bottom: 50px;
  }

  .hero_grid{
    margin-top: 50px;
  }

  .hero_title{
    font-size: 50px;
  }

  .hero_about{
    font-size: 20px;
    font-weight: 300;
  }


  .section::after{
    display: none;
  }

  .product_photo_item{
    padding: 10px 20px;
    padding-top: 0;
    filter: drop-shadow(5px 10px 3px rgba(0,0,0,0.3));
  }
}